@import 'home.scss' ;

// btn
.link-btn:link,
.link-btn:visited,
.btn{
    background: $color-primary-white;
    margin-top: 40px;
    color:$color-greey-dark;
    border-radius: 20px;
    text-decoration: none;
    display: inline-block;
    @extend %uppercase;
    padding: 15px 40px;
    border-radius: 100px;
    position: relative;
    animation-fill-mode: backwards;
    &:hover{
        transform: translateY(-5px);
        box-shadow: 0px 10px 10px rgba($color-black,.2);
    }
    &:active{
        transform: translateY(-1px);
        box-shadow: 0px 10px 10px rgba($color-black,.2);
    }
    &::after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-color:$color-primary-white;
        border-radius: 100px;
        display: inline-block;
        @extend %transition;
    }
    &:hover::after{
        transform:scaleX(1.4) scaleY(1.6);
        opacity: 0;
        
    }
}
.btn-last{
    background-color: $color-primary!important;
    color:#fff!important;
}
h2{
    font-size: 30px;
    @extend %uppercase;
    background-image: linear-gradient(to right,$color-primary-light , $color-primary-dark);
    background-clip: text;
    color: transparent;
    @extend %transition;
    font-weight: 700;
    @include m-bottom(80px);
    &:hover{
        transform: skewY(2deg) skewX(15deg);
    }
}
// btn
.header{
    background-image: linear-gradient(to right bottom,rgba($color-primary-light,.8) , rgba($color-primary-dark, 0.8)),
    url(../images/header.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    clip-path: polygon(0 0, 100% 0 , 100% 75vh, 0  100%);
    position: relative;
    z-index: 1;
    &.clip-path{
      clip-path: none;
    }
    .header-text-box{
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        text-align: center;
        z-index: 1;
        .header-primary{
            color: #fff;
            text-transform: uppercase;
            backface-visibility: hidden;
            margin-bottom: 50px;
            .header-primary-main{
                display: block;
                font-size: 60px;
                font-weight: 400;
                letter-spacing: 30px;
                animation:left 1s ease-in-out;
                line-height: 1.7;
            }
            .header-primary-sub{
                display: block;
                font-size: 20px;
                letter-spacing: 17px;
                font-weight: 600;
                animation:right 1s ease-in-out;
            }
        }
    }
    .btn{
        &:link,
        &:visited{
            text-decoration: none;
            color:$color-primary-white;
            text-transform: uppercase;
            padding: 15px 40px;
            background-color:$color-primary-white;
            color: $color-greey-dark;
            border-radius: 100px;
            display: inline-block;
            position: relative;
            animation:bottom 1s ease-out .58s;
            animation-fill-mode: backwards;
            @extend %transition;
        }
        &:hover{
            transform: translateY(-5px);
            box-shadow: 0px 10px 10px rgba($color-black,.2);
        }
        &:active{
            transform: translateY(-1px);
            box-shadow: 0px 10px 10px rgba($color-black,.2);
        }
        &::after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: -1;
            background-color:$color-primary-white;
            border-radius: 100px;
            display: inline-block;
            @extend %transition;
        }
        &:hover::after{
            transform:scaleX(1.4) scaleY(1.6);
            opacity: 0;
            
        }
    }
    // navbar
    .navbar{
        padding: 30px 0px;
        .header-logo-box{
            img{
                height: 50px;
            }
        }

        // nav
        .slide-bar{
            position: fixed;
            left: 0;
            top:0;
            width: 100%;
            height: 100vh;
            background: rgba($color-black,.8);
            visibility: hidden;
            opacity: 0;
            z-index: 2;
            @extend %transition;
            &.open{
                visibility: visible;
                opacity: 1;
            }
            .search{
                position: fixed;
                transition: all .5s ease-in-out;
                cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 300px;
                height: 80px;
                top: 0px;
                padding: 0px 15px;
                font-size: 30px;
                z-index: 3;
              
               
            }
            .form{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;
                height: 100%;
                position: fixed;
                width: 100%;
                height: 100%;
                z-index: 8;
                background: rgba($color-black-dark,.9);
                left: 0;
                top: 0;
                opacity: 0;
                visibility: hidden;
                z-index: 4;
                &.open{
                    opacity: 1;
                    visibility: visible;
                }
                form{
                    width: 400px;
                    input{
                        border: none;
                        outline: none;
                        background: none;
                        border-bottom: 1px solid #fff;
                        width: 400px;
                        color:$color-primary-white;
                    }
                    .open-search-form {
                        font-size: 50px;
                        color: $color-primary-white;
                        text-align: center;
                        font-weight: 300;
                        cursor: pointer;
                    }
                }
            }
            nav{
                position: absolute;
                top: 0px;
                left:-100%;
                width: 300px;
                height: 100%;
                background-color: $color-primary-white;
                @extend %transition;
                &.left{
                    left: 0;
                }
                ul{
                    margin-top: 100px;
                    text-align: center;
                    font-size: 30px;
                    display: block;
                    a{
                        color: $color-greey-dark;
                        font-size: 16px;
                        text-decoration: none;
                        @include font-family('Nexa-light.otf') ;
                        cursor: pointer;
                        @extend %uppercase;
                    }
                }
            }
         }
        // toggle
        .toggle{
            position: fixed;
            width: 50px;
            height: 50px;
            top: 30px;
            right: 35px;
            cursor: pointer;
            background-color:$color-primary-white;
            border-radius: 50%;
            span{
                position: absolute;
                width: 50%;
                height: 2px;
                @extend %center;
                background-color: $color-black-dark;
                display: block;
                &:after,
                &::before{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    content: "";
                    @extend %center;
                    background-color:$color-black-dark;
                    @extend %transition;
                }
                &:after{
                    top:10px;
                }
                &::before{
                    top:-10px;
                }
            }
        }
    }
    // @media (min-width:991px)
    @media(max-width:1200px){
        .header-primary-main {
            @include fonts-style(50px) ;
            @include letter-space(20px);
        }
        .header-primary-sub{
            @include fonts-style(18x) ;
            @include letter-space(10px);
        }
    }

}  
// header
// about-us
.about-us{
    margin-top: -20vh;
    padding: 150px 0px;
    h2{
        font-size: 30px;
        @extend %uppercase;
        background-image: linear-gradient(to right,$color-primary-light , $color-primary-dark);
        background-clip: text;
        color: transparent;
        @extend %transition;
        font-weight: 700;
        @include m-bottom(80px);
        &:hover{
            transform: skewY(2deg) skewX(15deg);
        }
    }
    .title{
        color:$color-black-dark;
        @extend %uppercase;
        font-size: 20px;
        @include m-bottom(20px);
    }
    .paragraph{
        font-size: 16px;
        color: $color-greey-dark;
        font-weight: 500;
        @include font-family('Nexa-light.otf');
        
    }
    .link{
        &:link,
        &:visited{
            font-size: 16px;
            border-bottom: 1px solid $color-primary;
            color: $color-primary;
            text-decoration: none;
            padding: 3px;
            @include font-family('Nexa-bold.otf');
            i{
                margin-left: 10px;
                font-size: 14px;
            }
        }
        &:hover{
            color: $color-primary-white;
            background-color: $color-primary;
            box-shadow: 1px 5px 10px $color-black;
            @extend %transition;
            padding: 5px 10px;
            transform: translateY(2px);
        }
        &:active{
            transform: translateY(0);
        }
    }
    .img{
        overflow: hidden;
        width: 100%;
        height: 100%;
        &-photo{
            width: 100%;
            height: 100%;
            border-radius: 3px;
           transition: all .7s ease-in-out;
           box-shadow: 0px 15px 20px rgba($color-black,.4);
            z-index: 10;
            outline-offset: 10px;
           
        &:hover{
            transform: scale(1.05);
            z-index: 20;
            
        }
        }
        &:hover .img-photo:not(:hover){
             transform: scale(.8);
        }
    }
    // @media (min-width:991px)
    @media(max-width:1200px){
        .title{
            @include fonts-style(18px) ;
        }
        .paragraph{
            @include fonts-style(14x) ;
        }
    }
}
