        }
        .card-photo-3{
            background-image: linear-gradient(to right bottom,rgba($color-thirty-light,.5) , rgba($color-primary-dark, 0.8)),
            url(../images/7.png);
        }
        & .card-head{
            .title{
                font-size: 30px;
                position: absolute;
                top: 120px;
