                    text-align: center;
                    font-weight: 500;
                    @include font-family('Nexa-bold.otf');
                }
            
        }
    }
    // card front
    // card back
           & .card-slide{
             .card-details{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
                text-align: center;
