            }
            & .card-slide-back-2{
                background-image: linear-gradient(to right bottom,$color-primary-dark, $color-primary-light );
            }
            & .card-slide-back-3{
                background-image: linear-gradient(to right bottom , $color-thirty-light,$color-thirty-white );
            }
            & .card-slide-back{
                transform: rotateY(-180deg);
            }
            &:hover .card-slide-front{
                transform: rotateY(180deg);
            }
            &:hover .card-slide-back{
                transform: rotateY(0deg);
            }
    }
     // popup
     .popup{
        position: fixed;
        width: 100%;
        background:$color-black-dark;
        height: 100vh;
        z-index: 999999;
        top: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
        overflow: hidden;
        @extend %transition;
        
        .popup-inner{
            @include width(90%);
            margin:auto;
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: absolute;
            transform: translate(-50%,-50%);
            top: 50%;
            left: 50%;
            background-color:$color-primary-white;
            height: 80%;
            overflow: hidden;
            .close{
                position: absolute;
                right: 10px;
                top: 0;
                font-size: 30px;
                color: #000;
                cursor: pointer;
                font-weight: bolder;
            }
            .popup-photo{
              @include width(30%);
              overflow: hidden;
              img{
                @include width(100%);
                height: auto;
              }
            }
            .popup-body{
                @include width(70%);
                padding: 0px 30px;
                text-align: left;
                color:$color-primary-white;
                
                h2{
                    margin: 20px 0px;
                    color: $color-primary;
                }
                h3{
                    font-size: 18px;
                    color:$color-black-dark;
                    @extend %uppercase;
                    @include font-family('Nexa-bold.otf');
                   @include m-bottom(20px); 
                }
                .pargraph{
                    display: flex;
                    justify-content: space-between;
                    color: $color-greey-dark;
                    text-align: justify;
                    column-gap: 50px;
                    p{
                       font-size: 16px;
                       @include font-family('Nexa-bold.otf');
                    }
                }
                .btn-last{
                    margin-top: 30px;
                    background-color: $color-primary!important;
                    color:$color-primary-white!important;
                }
            }
        }
    }
    .popup:target{
        visibility: visible;
        opacity: 1;
    }
}
    
