// people-say
.say-people{
    position: relative;
    width: 100%;
    height: 100vh;
    h2{
        margin: 30px 0px;
    }
    video{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        object-fit: cover;
        z-index: 1;
    }
    .overlay{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        background-color: $color-primary-white;
        opacity: .7;
        .slide{
            @include width(60%);
            margin: 30px auto 0;
            transform: skew(-15deg);
            background-color:$color-primary-white;
            padding: 15px;
            border:1px solid rgba(0,0,0,.125);
            transition: all .7s ease-in-out;
        
            & > *{
                transform: skew(15deg);

            }
            .card-head{
               width: 100px;
               height: 100px;
               border-radius: 50%;
               float: left;
                shape-outside: circle();
                margin: 18px;
                overflow: hidden;
                position: relative;
                .photo{
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    transition: all .6s ease-in-out;  
                    &::after{
                        position: absolute;
                        content:'MARY SMITH';
                        left: 50%;
                        top: 50%;
                        transform: translate(-50%,-50%);
                        opacity: 0;
                        color: #fff;
                        transition: all .6s ease-in-out; 
                    }
                }
               img{
                   width:100%;
                   height:100%;
                   border-radius: 50%;
                   object-fit: cover;
                   transition: all .6s ease-in-out; 
               }  
               &:hover img{
                transform: scale(1.5);
                cursor: pointer;
                background-image: -webkit-radial-gradient( 50% 50%, circle closest-side, rgb(151,151,150) 0%, rgb(195,11,42) 0%, rgb(173,12,39) 100%);
                background-image: -ms-radial-gradient( 50% 50%, circle closest-side, rgb(151,151,150) 0%, rgb(195,11,42) 0%, rgb(173,12,39) 100%);
                opacity: 0.549;
               }
               &:hover .photo::after{
                opacity: 1;

               }
            }
            }
        
            .card-bodys{
                position: relative;
                z-index: -1;
                h3{
                    color:$color-black-dark;
                    font-size: 20px;
                    @extend %uppercase;
                    @include font-family('Nexa-light.otf');
                    font-weight:700;
                    padding-top: 20px;
                    @include m-bottom(15px);
                }
                p{
                    color:$color-greey-dark;
                    @include font-family('Nexa-bold.otf');
                    font-size: 16px;
                }
            }
        }
        // @media(max-width:1200px)
        @media(max-width:1200px){
            .slide{
                @include width(80%!important);
            }
            .card-bodys h3{
                @include fonts-style(18px) ;
            }
        }
    }

// contact
.contact-us{
    background-image: linear-gradient(to right bottom,#46ce59 , #00b176);
    padding: 100px 0px;
    .contact{
        background-image: url('../images/15.png');
        background-size: cover;
        height: 400px;
        @include width(70%);
        margin: auto; 
        .form{
            position: relative;
            width: 60%;
            height: 100%;
            overflow: hidden;
            z-index: 1;
        &::before{
            content: '';
            position: absolute;
            left: -70px;
            top:0;
            width: 100%;
            height: 100%;
            background: #fff;
            opacity: .9;
            transform: skewX(-20deg);
           transform-origin: left;
           z-index: -1;
        }
        h2{
            margin:20px 40px 50px;
        }
        input[type='text'],
        input[type= 'email']{
            width: 60%;
            margin:20px 40px;
            display: block;
            height: 40px;
            background-color:#fff;
            color: #999;
            border: none;
            padding: 20px;
            &::placeholder{
                color: #ccc;
                font-weight: 400;
                
            }
            &:focus{
                border: none;
               outline: none;
            }
        }

        input[type=radio]{
            display: none;
            
            }
            .radio{
                margin:30px 0 0 50px;
                position: relative;
                font-size: 18px;
                color: #ccc;
                font-weight: 400;
                line-height: 1.5;
                @include font-family('Nexa-light.otf');
                cursor: pointer;
            }
           & span{
                position: absolute;
                left:-30px;
                top:5px;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                border: 3px solid $color-primary-dark;
                &::after{
                    content: '';
                    position: absolute;
                    left:50%;
                    top:50%;
                    width: 7px;
                    height: 7px;
                    border-radius: 50%;
                    transform: translate(-50%,-50%);
                    opacity: 0;
                    background-color: $color-primary-dark;
                    transition: all .1s ease-in-out;
                    display: block;
                }
            }
         }
         
         & input[type=radio]:checked ~ span::after{
            opacity: 1;
        }
        button{
            @extend %button;
            display: block;
            margin:30px 40px;
        }
    }
    // @media(max-width:1200px)
    @media(max-width:1200px){
        .contact{
            @include width(90%!important);
        }
        h2{
            @include m-bottom(40px)
        }
    }
}
    

