// footer
footer{
    background-color: $color-black-dark;
    padding: 60px 0px;
    .col-md-12{
        display: flex;
        align-items: center;
        justify-content: center;
        @include  m-bottom(80px) 
    }
    .col-md-5{
        @include m-left(4%);
        margin-right: 4%;
        padding: 0;
        &::before{
            content: '';
            position: absolute;
            top:-15px;
            left:0;
            width: 100%;
            height: 1px;
            background-color: $color-greey-dark;
        }
        .links{
            display: flex;
            align-items: flex-start;
            justify-content: space-between;

            @extend %uppercase;
            a{
                color:$color-primary-white;
                @extend %link;
                @extend %transition;
                padding-top: 10px;
                
        font-size: 16px;
                
                &:hover{
                    color: $color-primary;
                    
                }
            }
        }
        p{
            
        font-size: 16px;
            color:$color-primary-white;
            @include font-family('Nexa-light.otf');
            padding-top: 10px;
        }
    }
    
}

    // key frames
    @keyframes left{
        0%{
            opacity: 0;
            transform: translateX(-100px);
        }
        80%{
            transform: translateX(10px);
        }
        100%{
            opacity: 1;
            transform: translateX(0);
        }
    }
    @keyframes right{
        0%{
            opacity: 0;
            transform: translateX(100px);
        }
        80%{
            transform: translateX(-10px);
        }
        100%{
            opacity: 1;
            transform: translateX(0);
        }
    }
    @keyframes bottom{
        0%{
            transform: translateY(30px);
            opacity: 1;
        }
        80%{
            transform: translateY(10px);
    
        }
        100%{
            transform: translateY(0);
            opacity: 1;
        }
    }
    html.active{
        overflow: hidden;
    }
//   keyframes




