// colors
$color-primary: #00c269;
$color-primary-light:#7ed56f;
$color-primary-dark:#28b485;
$color-primary-white:#fff;
$color-greey-dark:#777;
$color-black:#000;
$color-black-dark:#333;
$color-blue:#eee;
$color-second-light:#ffb900;
$color-second-white:#ff7730;
$color-thirty-light:#2998ff;
$color-thirty-white:#5643fa;

// variable
%Nexa-light{
    font-family: 'Nexa-light.otf';
}
%center{
    top: 50%;
    left: 50%;
    transform: translate(-50% ,-50%);
}
%transition{
    transition: all .5s ease-in-out;
}
%uppercase{
    text-transform: uppercase;
}
%link{
    text-decoration: none;
}
%button{
    border: none;
    outline: none;
}
@mixin font-family($font) {
    font-family:$font;
}
@mixin fonts-style($fonts) {
    font-size:$fonts!important;
}
@mixin letter-space($letter) {
    letter-spacing: $letter!important;
}
@mixin m-bottom($m) {
    margin-bottom:$m!important;
}
@mixin m-left($l) {
    margin-left:$l!important;
}
@mixin width($w) {
    width:$w;
}